export default {
  name:'searchBox',
  data(){
    return{
      parentSearchValue:this.searchValue,
    }
  },
  props:['isBackBtn','isSearchButtonShow','searchValue'],
  computed:{
      isBackShow:function(){
          if(this.isBackBtn == 'false'){
              return false
          }else{
              return true
          }
      },
  },
  methods:{
    goSearchMain:function(){
      if(this.$router.history.current.path == '/index' || this.$router.history.current.path == '/'|| this.$router.history.current.path == '/allProduct'){
        this.$router.push({path:'/searchMain'})
      }else{
          return
      }
    },
    goBack:function(){
      this.$router.goBack();
    },
    seacrhNow:function(){
      var item = this.parentSearchValue;
      this.$emit('searchNow',item);
    },
    goMyMessage:function(){
      this.$router.push({path:'/myMessageInfo'})
    }
  }
}



