    import PullTo from 'vue-pull-to'
    export default {
      name:'productListPage',
      data(){
        return{
          viewChage:true,
          upDownShow:true,
          upDown:0,
          allSortActive:true,
          priceSortActive:false,
//          nowList:this.listData,
          islistView:false
        }
      },
      updated:function(){
//        this.nowList=this.listData;
      },
      computed:{
          nowList:{
              get:function(){
                return this.listData
              },
              set:function(v){
                this.nowList.push(...v);
              }
          }
      },
      props:['listData'],
      components: {
        PullTo
      },
      methods:{
        changeView:function(){
          this.viewChage = !this.viewChage;
          this.islistView = !this.islistView;
        },
        allSortDo:function(){
          this.allSortActive=true;
          this.priceSortActive=false;
          this.upDownShow = true;
          this.nowList = sortByKey(this.nowList,'pid')
          function sortByKey(array, key) {
            return array.sort(function(a, b) {
              var x = a[key];
              var y = b[key];
              return ((x < y) ? -1 : ((x > y) ? 1 : 0));
            });
          }
        },
        priceSortDo:function(){
          this.allSortActive=false;
          this.priceSortActive=true;
          this.upDownShow = false;
          this.upDown++;
          if(this.upDown %2 == 0){
            this.nowList = sortByKey(this.nowList,'proPrice')
          }else{
            this.nowList = sortByKey(this.nowList,'proPrice').reverse();
          }
          function sortByKey(array, key) {
            return array.sort(function(a, b) {
              var x = parseInt(a[key]); var y = parseInt(b[key]);
              return ((x < y) ? -1 : ((x > y) ? 1 : 0));
            });
          }
        },
        refresh(loaded) {
          setTimeout(() => {
            this.nowList.unshift(this.nowList[0]);
            loaded('done');
          }, 2000);
        },
        geMore(loaded){
            setTimeout(()=>{
                //console.log(this.nowList)
                this.nowList = this.listData;
                loaded('done')
            },2000)
        },
        goInformation:function(pid){
          this.$router.push({path:'/proInformation?pid='+pid})
        }
      }
    }



